import "./styles.css";

const form = document.querySelector("form");
// form.innerHTML = 'form!'
const ul = document.querySelector("ul");

form.addEventListener("submit", (e) => {
  e.preventDefault();
  // form.innerHTML = "form!";
  const data = new FormData(e.target);
  ul.innerHTML = "<li>" + data.get("todo") + "</li>" + ul.innerHTML;
  // если добавить ul.innerHTML перед "<li>", тогда новое слово будет добавляться в конце списка
  form.reset();
});
